#book_search {
	text-align: center;
	background: var(--gradient-light);
	&:hover, &:active, &:focus {
		box-shadow: var(--shadow);
	}
}

#book-search-results {
	display: flex;
	flex-wrap: wrap;
	flex-direction: row;
	justify-content: center;
	align-items: center;
	max-width: 98vw;
	margin: 20px 1vw 0 0;
	padding: 0;
	li {
		display: inline-block;
		width: 240px;
		margin: 0 50px 10px;
		padding: 10px;
		.description-wrapper {
			position: relative;
		}
		.description {
			display: flex;
			position: relative;
			top: 22px;
			left: 75px;
			margin: 0;
			font-weight: bold;
			z-index: 1;
			font-size: 0.8rem;
			padding: 7px 0 0 10px;
			text-align: left;
			height: 20px;
			width: 65px;
			color: black;
			background: var(--gradient2);
			border-radius: 10px 10px 0 0;
			z-index: 2;
			span {
				display: flex;
				justify-content: space-around;
				align-items: center;
				flex-wrap: wrap;
				margin: 0 0 10px;
				a {
					border: 1px solid black;
					color: black;
					transition: color 0.1s ease;
					&:hover {
						color: white;
						// border: 1px solid skyblue;

					}
				}
			}
			.hidden-description {
				display: block;
				position: absolute;
				top: 27px;
				left: 0px;
				width: 65px;
				max-height: 300px;
				overflow-y: auto;
				border-radius: 25px;
				font-size: 0.8rem;
				font-style: italic;
				background: var(--gradient3);
				color: black;
				z-index: 2;
				height: 0;
				text-overflow: ellipsis;
				transition: height 0.15s ease, padding 0.15s ease, width 0.1s ease, left 0.1s ease;
				transition-delay: 0.05s;



				p {
					display: flex;
					flex-direction: column;
					justify-content: center;

					span {
						text-align: left;
						margin: 0;
					}

					.categories, .pages, .published {
						display: inline-block;
					}

					.synopsis {
						margin-top: 5px;
					}
					}
					.synopsis {
						font-weight: normal;
						font-style: italic;
					}
				}
			}

			.description:hover .hidden-description {
				padding: 12px;
				height: 280px;
				width: 250px;
				left: -100px;
				// overflow: scroll;
			}
			figure {
				margin: 10px 10px 80px;
				padding: 0;
				display: flex;
				align-items: bottom;

				div {
					position: relative;
					top: -16px;
					border-radius: 0;
					border: none;
					display: block;
					z-index: 1;
					width: 200px;
					height: 300px;
					transition: transform 0.2s ease;
				}

				div:hover {
					transform: scale(1.15, 1.15);
				}
			}
		
	}
}

