#logo {
	position: fixed;
	z-index: -1;
	width: 400px;
	max-width: 98vw;
	height: auto;
	top: 120px;
	right: calc(50vw - 200px);
	opacity: 0.15;
	transition: width 0.2s ease-out, opacity 0.2s ease-out, right 0.2s ease-out, top 0.2s ease-out;
}

.logo-appear {
	opacity: 1 !important;
	z-index: -1;
}

#notice {
	opacity: 1;
	position: fixed;
	top: -130px;
	left: 25vw;
	background: rgba(107, 209, 251, 0.9);
	width: 50vw;
	padding: 15px 20px;
	margin: 0 auto;
	border-radius: 25px;
	text-align: center;
	font-size: 1.2rem;
	color: black;
	z-index: 10;
	transition: opacity 1s ease, z-index 2s ease;
	transition-delay: 3s;
	-webkit-animation-name: moveMessage;
    -webkit-animation-duration: 3s; 
    animation-name: moveMessage;
}

@keyframes moveMessage {
    0%   {top: 80px;}
    80%  {top: 80px;}
    100% {top: -130px;
    display: none;
    visibility: none;
    }
}

