// -------------------------- RANGE INPUT MIXINS --------------------------

// Codepen to test this out: https://codepen.io/prupke/pen/wNVvJX




@mixin range-inputs() { 
	background: skyblue; 
	font-size: 13rem;
	margin: 5px;
	padding: 0;
	width: 150px;
	height: 30px;
}

// set the width and height of the range input here
$input-w: 12rem;
$input-h: 2.0rem;
$track-h: .625*$input-h;
$thumb-d: .8125*$input-h;
$filll-h: .3125*$input-h;

// Adjust this if the tracker is misaligned (happens with webkit browsers)
$sw: 0*$track-h;

@mixin range-input-highlight-boxes($c: white, $a: .9) {
	box-sizing: border-box;
	// border: 2px solid skyblue;
	padding: none;
	border-radius: 25px;
	background: currentcolor content-box;
	color: rgba($c, $a);
}

@mixin range-input-track() {
	height: $track-h;
	@include range-input-highlight-boxes(white)
}

@mixin range-input-fill() {
	height: $filll-h;
	border: none;
	@include range-input-highlight-boxes(teal, .4)
}

@mixin range-input-thumb() {
	width: $thumb-d; height: $thumb-d;
	border-radius: 0;
	@include range-input-highlight-boxes(skyblue)
}

input[type='range'] {
	&, &::-webkit-slider-thumb {
		-webkit-appearance: none
	}
	border: none !important;
	margin: .25em auto;
	display: block;
	width: $input-w; height: $input-h;
	@include range-input-highlight-boxes;
	font-family: var(--font1);
	
	&::-webkit-slider-runnable-track {
		@include range-input-track()
	}
	&::-moz-range-track {
		@include range-input-track()
	}
	&::-ms-track {
		@include range-input-track()
	}
	
	&::-moz-range-progress {
		@include range-input-fill()
	}
	
	&::-ms-fill-lower {
		@include range-input-fill()
	}
	
	&::-webkit-slider-thumb {
		margin-top: .5*($track-h - $thumb-d) - 2*$sw;
		@include range-input-thumb()
	}
	&::-moz-range-thumb {
		@include range-input-thumb()
	}
	&::-ms-thumb {
		margin-top: 0;
		@include range-input-thumb()
	}
}

